.container {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: url("./assets/background.jpg") no-repeat;
  background-size: cover;

  @keyframes showContainer {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  animation: showContainer .3s ease forwards;
}

.modal {
  display: inline-block;
  min-width: 500px;
  padding: 40px;
  box-shadow: 0px 0px 4px 0px rgba(188,185,208,1);
  border-radius: 12px;
  background: #ffffff;

  @media (max-width: 500px) {
    width: 100%;
    min-width: auto;
    padding: 40px 20px;
  }

  @keyframes showModal {
    from {
      opacity: 1;
      transform: translateY(-30px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  animation: showModal .3s ease forwards;

  .successButton {
    align-self: flex-end;
  }
}

.title {
  font-size: 28px;
  font-weight: 500;
  text-align: center;
}

.form {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  .inputContainer {
    &:not(:first-child) {
      margin-top: 15px;
    }
  }
}

.inputContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  .fieldError {
    margin-bottom: 8px;
    color: #EB4040;
    font-size: 12px;
  }
  .input {
    display: block;
    height: 40px;
    font-family: inherit;
    font-size: 1rem;
    border-radius: 12px;
    outline: none;
    border: solid 1px rgba(188,185,208, 0.4);
    padding: 8px 0 8px 15px;
    &::placeholder {
      color: rgba(65, 68, 100, 0.4);
    }
  }
}

.actions {
  margin-top: 30px;
  align-items: center;
  @media (max-width: 600px) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .successButton {
      align-self: auto;
      margin-top: 20px;
    }
  }
}

.link {
  text-decoration: none;
  color: #246EDE;
  &:hover {
    color: #4880D4 !important;
  }
}

.row {
  display: flex;

  &.spaceBetween {
    justify-content: space-between;
  }
}




