.button {
  background: transparent;
  outline: none;
  border: none;
  cursor: pointer;
  &:disabled {
    cursor: default;
  }
}

.success {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 16px;
  min-width: 110px;
  background: #246EDE;
  color: #ffffff;
  width: fit-content;
  transition: background .2s ease;
  font-size: 13px;
  &:hover {
    background: #144FA7;
  }
  &:disabled {
    background: #4880D4 !important;
  }
}
