.screen {
  position: relative;
  width: 100vw;
  height: 100vh;
  max-height: 100vh;
  overflow: hidden;
}

.sendFileButton {
  position: absolute;
  margin-top: 10px;
  width: 100%;
  padding: 12px;
  bottom: 0;
}

.error {
  display: block;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  color: #EB4040;
  font-size: 12px;
  padding: 10px 20px;
  border-radius: 12px;
  background: #ffffff;
  box-shadow: 0px 2px 10px rgba(27, 24, 24, 0.14);
}

.commentField {
  resize: none;
  width: 250px;
  height: 70px;
  padding: 5px 8px;
  border: solid 1px #BDBDBD;
  outline: none;
  margin-top: 10px;
  border-radius: 8px;
}
