.container {
  padding: 10px;
}

.list {
  display: flex;
  flex-direction: column;
  margin-top: 10px;
  border: solid 1px #BDBDBD;
  border-radius: 8px;
  padding: 5px 12px;
  max-height: 400px;
  overflow-y: auto;
}

.mainTitle {
  font-size: 20px;
  font-weight: 500;
}

.mainRow {
  display: flex;
  width: 100%;
  cursor: pointer;
  padding: 10px 20px 10px 5px;
  flex-direction: column;
  &:hover {
    background: #F6F7FF;
  }
}

.row {
  display: flex;
  align-items: center;
}

.filename {
  font-size: 14px;
  font-weight: 500;
}

.timestamp {
  font-size: 10px;
  color: #E3E3E3;
  margin-left: auto;
}

.comment {
  font-size: 13px;
  margin-top: 5px;
  span {
    display: block;
    color: rgba(27, 39, 51, 0.4);;
    margin-top: 5px;
  }
}
